.App {
  text-align: center;
}

.App-header {
  background-color: #222;
  height: 50px;
  padding: 25px;
  color: white;
  border-bottom: 2px solid rgb(36, 191, 6);
}

input, select, textarea{
    width: 100%;
    height: 30px;
    border: 2px solid rgb(24, 121, 191, .5);
    border-radius: 5px;
    padding: 5px;
    font-size: 15pt;
}

select{
    width: 101%;
    height: 50px;
}

textarea{
    height: 80px;
    resize: none;
}

button{
    height: 50px;
    width: auto;
    min-width: 120px;
    padding: 10px;
    border: 2px solid;
    border-radius: 5px;
    font-size: 15pt;
    color: black;
    font-weight: 800;
    margin: 10px;
    border: 2px solid black;
}

button:hover{
    cursor: pointer;
    color: white;
    -webkit-transition: .250s;
    -o-transition: .250s;
    transition: .250s;
}

.input-section{
    margin: 10px 0;
    width: 80%;
    position: relative;
    margin: auto;
    text-align: center;
}

.input-section label{
    font-size: 15pt;
}

.input-section p{
    text-align: left;
}

.container{
    width: 95%;
    max-width: 1280px;
    position: relative;
    margin: auto;
    border: 1px solid rgba(0, 0, 0, .2);
    padding: 20px 0;
    background: white;
    border-radius: 2px;
}

.danger{
    background: #d62617;
    -webkit-box-shadow: 3px 5px 0px #a5190d;
            box-shadow: 3px 5px 0px #a5190d;
}

.danger:hover{
    background: #b51b0e;
}

.success{
    background: #17d670;
    -webkit-box-shadow: 3px 5px 0px #0e8c48;
            box-shadow: 3px 5px 0px #0e8c48;
}

.success:hover{
    background: #09994b;
}

input:focus, textarea:focus, select:focus{
    border-color: rgb(36, 191, 6);
}

.side-menu{
    width: 280px;
    height: 50vh;
    text-align: center;
    position: absolute;
    background: #fff;
    border-right: 1px solid rgba(0, 0, 0, .2);
    border-bottom: 1px solid rgba(0, 0, 0, .2);
}

.lista{
    width: 100%;
    text-align: left;
    padding: 0;
}

.lista li{
    list-style: none;
    height: 30px;
    padding: 20px 0 0 20px;
    text-align: left;
    position: relative;
    margin: auto;
    font-weight: 600;
}

.lista li:hover{
    background: #e0dbdb;
}

a{
    color: black;
    text-decoration: none;
}
a:hover{
    color: #24bf06;
    text-decoration: underline;
}



